let fb_refresh = () => {

    let rootRef = firebase.database().ref();

    fb_writable = true;
    fb_response = {};

    (() => {
        rootRef.on('value', function(snapshot) {
            var obj = snapshot.val();
            state.data.firebase = obj;
            console.log('fb_refresh(): has updated -> state.data.firebase');
            console.log(state.data.firebase);
        });

    })();
};
